@import 'lib/_reset';
// @import 'lib/_normalize';

// Config

@import 'config/_variables';
@import 'config/_utils';
@import 'config/_fonts';

// Global style
html {
  	box-sizing: border-box;
	font-family: 'Favorit',sans-serif;
	font-weight: normal;
}

*,
*::before,
*::after {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: inherit;
}

#content {
  position: relative;
  max-width: 100vw;
  overflow: hidden;
}

.main-wrapper {
  max-width: $gridSizeLarge;
  margin: 0 auto;
  width: 100%;

  @include desktop{
	max-width: $gridSize;
	// margin: 0 40px;
  }
  @include laptop {
    // margin: 0 30px;
  }

  @include mobile {
    // margin: 0 15px;
  }

  @include tablet {
    // margin: 0 auto;
  }
}


// Padding
.padding-small{
	padding: 0 40px;
}
.padding-medium{
	padding: 0 60px;

	@include mobile-xl{
		padding: 0 40px;
	}
	@include mobile{
		padding: 0 20px;
	}

}
.padding-big{
	padding: 0 80px;
}
.padding-large{
	padding: 0 120px;
}




img {
  max-width: 100%;
}

a {
  color: inherit;
}

.mobile-only {
  display: none;

  @include mobile {
    display: block;
  }
}

//TITRES
.title-banner{
	font-size: 40px;
	line-height:56px;
	font-family: 'Favorit Bold Lining', sans-serif;
	@include laptop{
		font-size: 25px;
		line-height:40px;
	}
}

.title{
	font-size: 36px;
	line-height:56px;
	font-family: 'Favorit Bold Lining', sans-serif;
	@include laptop{
		font-size: 25px;
		line-height:40px;
	}
}

.title--secondary{
	font-size: 25px;
	line-height:40px;
	font-family: 'Favorit Bold Lining', sans-serif;
}

.title--secondary-unline{
	font-size: 20px;
	line-height:24px;
	font-family: 'Favorit', sans-serif;
	@include laptop{
		font-size: 18px;
		line-height: 22.48px;
	}
	@include tablet{
		font-size:15px;
		line-height: 24px;
	}
}

//SURTITRE
.surtitle{
	font-size: 30px;
	line-height:37.5px;
	font-weight: 400;
	font-family: 'Favorit', sans-serif;
	letter-spacing: -0.1px;
	@include laptop-small{
		font-size: 18px;
		line-height:24px;
	}
}

//POSTES
.job{
	font-size: 26px;
	line-height:35px;
	letter-spacing: -0.1px;
	font-family: 'Favorit',sans-serif;
}

//DESCRIPTION
.description{
	font-size: 18px;
	line-height:24px;
	letter-spacing: -0.1px;
	font-family: 'Favorit',sans-serif;
	// @include desktop{
	// 	font-size: 15px;
	// 	line-height: 22px;
	// }
	@include tablet{
		font-size:18px;
		line-height: 24px;
	}
} 
.description-light{
	font-size: 18px;
	line-height:24px;
	letter-spacing: -0.1px;
	font-family: 'Favorit',sans-serif;
	font-weight: 300;
	@include tablet{
		font-size:18px;
		line-height: 24px;
	}
}

//BOLDeltigre
.bold{
	font-weight: bold;
}
//MEDIUM
.medium{
	font-weight: 500;
}
//UPPERCASE
.uppercase{
	text-transform: uppercase;
}
// TEXT-ALIGN
.left{
	text-align: left;
}
.center{
	text-align: center;
}
.right{
	text-align: right;
}
.justified{
	text-align: justify;
}
// BORDER-RADIUS DES SECTIONS
.radius-top{
	border-top-left-radius:40px;
	border-top-right-radius:40px;
	@include mobile-xl{
		border-top-left-radius:0px;
		border-top-right-radius:0px;
	}
}
.radius-bottom{
	border-bottom-left-radius:40px;
	border-bottom-right-radius:40px;
	@include mobile-xl{
		border-bottom-left-radius:0px;
		border-bottom-right-radius:0px;
	}
}
.radius-top-bottom{
	border-radius: 40px;
	@include mobile-xl{
		border-radius: 40px;
	}
}
.radius-three-corner{
	border-top-left-radius:40px;
	border-top-right-radius:40px;
	border-bottom-right-radius:40px;
	@include mobile-xl{
		border-top-left-radius:0px;
		border-top-right-radius:0px;
		border-bottom-right-radius:0px;
	}
}



